::ng-deep .mapButton {
  background: none rgb(255, 255, 255);
  border: 0px;
  margin: 10px;
  padding: 7px 8px;
  text-transform: none;
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  border-radius: 2px;
  height: 40px;
  box-shadow: #0000004d 0 1px 4px -1px;
  overflow: hidden;
}

::ng-deep .mapButton .mapButtonLabel {
  font-size: 18px;
  margin: 0px;
  padding: 0px 9px;
  color: rgb(86, 86, 86);
  text-transform: none;
  width: 100px;
  display: block;
}

::ng-deep .mapButton.active {
  background: #eee;
  box-shadow: 1px 1px 2px #ccc inset;
}

::ng-deep .mapButton.active .mapButtonLabel {
  color: rgb(0, 0, 0);
  font-weight: 500 !important;
}

::ng-deep google-map > .map-container {
  inset: 0px;
  width: 100% !important;
  height: auto !important;
  position: absolute;
  overflow: hidden;
}

::ng-deep button.mapButton img {
  width: 24px;
  height: 24px;
}
